.wrapper {
    background: #F2F2F2;

    .container {
        display: flex;
        flex-wrap: wrap;

        .left {
            width: calc(100% - 650px);
            display: flex;
            flex-direction: column;
            justify-content: center;
            padding: 0 45px;

            h2 {
                font-weight: bold;
                font-size: 24px;
                font-family: 'Gilroy bold', sans-serif;
                text-align: center;
                text-transform: uppercase;
            }

            .info{
                text-align: center;
                margin: 22px auto 11px;
                width: 330px;
                font-weight: 500;
                font-size: 17px;
                line-height: 23px;
                text-align: center;
                text-align: center;
                color: #304FFF;
            }

            input {
                padding: 20px 25px;
                width: 100%;
                outline: none;
                border: 1px solid rgba(151, 151, 151, 0.482592);
                border-radius: 5px;
                font-size: 19px;
                margin-bottom: 24px;
            }

            label {
                font-weight: 500;
                font-size: 17px;
                color: #787993;
                margin-bottom: 7px;
                display: inline-block;
            }

            button {
                width: 100%;
                padding: 20px 0;
                font-size: 17px;
                color: #FFFFFF;
                background: #41D481;
                border-radius: 5px;
                border: none;
                outline: none;
                margin-bottom: 50px;
            }

            .bottom {
                text-align: center;
                width: 100%;
                color: #787993;
                position: relative;

                span {
                    &::before {
                        content: '';
                        width: 100%;
                        top: 0;
                        bottom: 0;
                        width: 45%;
                        position: absolute;
                        height: 2px;
                        background: #787993;
                        left: 0;
                        margin: auto 0;
                    }

                    &::after {
                        content: '';
                        width: 100%;
                        top: 0;
                        bottom: 0;
                        width: 45%;
                        position: absolute;
                        height: 2px;
                        background: #787993;
                        right: 0;
                        margin: auto 0;
                    }
                }
            }

            a {
                margin-top: 10px;
                font-weight: bold;
                font-size: 16px;
                text-align: center;
                text-decoration: none;
                font-family: 'Gilroy bold', sans-serif;
                color: #4E72CA;
            }
        }

        .right {
            width: 650px;
            height: 950px;
            background: url(../../assets/images/Login-bg.png) no-repeat;
            background-size: cover;
            position: relative;
            z-index: 1;

            .extra {
                width: 275px;
                height: 275px;
                background: url(../../assets/images/login-small.svg) no-repeat;
                background-size: cover;
                position: absolute;
                bottom: 185px;
                left: 60px;
                padding: 79px 42px 0 42px;

                p {
                    font-family: 'Gilroy bold', sans-serif;
                    font-weight: bold;
                    font-size: 19px;
                    color: #787993;
                    margin: 7px 0 18px;
                }

                a {
                    display: inline-block;
                    text-decoration: none;
                    color: #41D481;
                    padding: 9px 18px;
                    border: 2px solid #41D481;
                    border-radius: 5px;
                    font-weight: bold;
                    font-size: 16px;
                    font-family: 'Gilroy bold', sans-serif;
                }
            }
        }
    }
}